import { InjectedConnector } from '@web3-react/injected-connector';
import { WalletConnectConnector } from '@web3-react/walletconnect-connector';
import { useWeb3React } from '@web3-react/core';
import web3 from 'web3';
import React from 'react';

import { chains } from '@/utils/chainData';

const injected = new InjectedConnector({
  supportedChainIds: chains.map(({ chainId }) => chainId)
});

const walletConnect = new WalletConnectConnector({
  rpc: (() => {
    const returnObj: { [key: string]: string } = {};
    chains.forEach(({ chainId, rpc, symbol }) => {
      returnObj[chainId] = rpc;
    });
    return returnObj;
  })(),
  qrcode: true
});

const Auth = (props: any) => {
  const context = useWeb3React();
  console.log(`debug -> Auth -> context`, context)

  const { library, account, activate, deactivate, active, error } = context;

  console.log(`debug -> updateBalance -> library?.network`, library?.network);
  console.log(
    `debug -> updateBalance -> window.library = library`,
    ((window as any).library = library)
  );
  console.log(
    `debug -> updateBalance -> window.web3 = web3`,
    ((window as any).web3 = web3)
  );

  return (
    <>
      {active ? (
        <>
          <p style={{ wordBreak: 'break-all' }}>{account}</p>
          <button
            onClick={() => {
              deactivate();
            }}
          >
            disconnect wallet
          </button>
          {props.children}
        </>
      ) : (
        <>
          <button
            onClick={() => {
              activate(injected);
            }}
          >
            connect injected wallet
          </button>
          <button
            onClick={() => {
              activate(walletConnect);
            }}
          >
            connect walletConnect
          </button>
          {error && <p>{error.message}</p>}
          {props.children}
        </>
      )}
    </>
  );
};

export default Auth;
